import { DOCUMENT } from '@angular/common';
import { Inject, Injectable, SecurityContext } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

import icons, { IconType } from './generated-svg/svg-icons';
import { SvgIcon, SvgIconType } from './svg-icon.interface';
import { SVG_REGISTRY_TOKEN } from './svg-icon.token';

/**
 * –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ—Ç –º–∞—Å—Å–∏–≤ —Å –∏–º–µ–Ω–µ–º –∏ –¥–∞–Ω–Ω—ã–º–∏ –≤ –æ–±—ä–µ–∫—Ç.
 * @param {Array} name - –°—Ç—Ä–æ–∫–∞ —Å –∏–º–µ–Ω–µ–º.
 * @param {Array} data - –°—Ç—Ä–æ–∫–∞ —Å –¥–∞–Ω–Ω—ã–º–∏.
 * @returns {Object} - –û–±—ä–µ–∫—Ç —Å —Å–æ–±—Å—Ç–≤–µ–Ω–Ω–æ—Å—Ç—è–º–∏ –∏–º–µ–Ω–∏ –∏ –¥–∞–Ω–Ω—ã—Ö.
 */
const objConverting = ([name, data]: [string, string]): { name: string; data: string } => ({ name, data });

/**
 * –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ—Ç –æ–±—ä–µ–∫—Ç –∏–∫–æ–Ω–æ–∫ –≤ –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤ –∏–∫–æ–Ω–æ–∫.
 *
 * @param {Object} obj - –û–±—ä–µ–∫—Ç, —Å–æ–¥–µ—Ä–∂–∞—â–∏–π –∏–∫–æ–Ω–∫–∏.
 * @returns {Array} –ú–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤ –∏–∫–æ–Ω–æ–∫, –≥–¥–µ –∫–∞–∂–¥—ã–π –æ–±—ä–µ–∫—Ç –∏–º–µ–µ—Ç —Å–≤–æ–π—Å—Ç–≤–∞ –∏–º–µ–Ω–∏ –∏ –¥–∞–Ω–Ω—ã—Ö.
 */
const iconsToArray = (obj: { [key: string]: string }): { name: string; data: string }[] => Object.entries(obj).map(objConverting);

/**
 * –°–ª—É–∂–±–∞ –¥–ª—è —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è –∏–∫–æ–Ω–∫–∞–º–∏ SVG.
 */
@Injectable({ providedIn: 'root' })
export class SvgIconService {
  /**
   * –ö–ª–∞—Å—Å –ø—Ä–µ–¥—Å—Ç–∞–≤–ª—è—é—â–∏–π –∫–∞—Ä—Ç—É SVG.
   * @class
   */
  private readonly svgMap = new Map<string, SvgIcon>();
  /**
   * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä –¥–ª—è –∫–ª–∞—Å—Å–∞.
   *
   * @constructor
   * @param {Document} document - –î–æ–∫—É–º–µ–Ω—Ç –¥–ª—è –º–∞–Ω–∏–ø—É–ª–∏—Ä–æ–≤–∞–Ω–∏—è DOM.
   * @param {Record<string, string>} iconsRegistry - –†–µ–µ—Å—Ç—Ä, —Å–æ–¥–µ—Ä–∂–∞—â–∏–π –∏–∫–æ–Ω–∫–∏ SVG.
   * @param {DomSanitizer} sanitizer - –û–±—ä–µ–∫—Ç-—Å–∞–Ω–∏—Ç–∞–π–∑–µ—Ä –¥–ª—è –æ—á–∏—Å—Ç–∫–∏ HTML –∫–æ–Ω—Ç–µ–Ω—Ç–∞.
   * @returns {void}
   */
  constructor(
    @Inject(DOCUMENT) private document: Document,
    @Inject(SVG_REGISTRY_TOKEN) private iconsRegistry: Record<string, string>,
    private sanitizer: DomSanitizer,
  ) {
    const inMemoryIcons = this.iconsRegistry ?? icons;
    if (inMemoryIcons) {
      this.register(iconsToArray(inMemoryIcons));
    }
  }

  /**
   * –ò–∑–≤–ª–µ–∫–∞–µ—Ç —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ SVG –¥–ª—è –∑–∞–¥–∞–Ω–Ω–æ–≥–æ –∫–ª—é—á–∞ –∏–∫–æ–Ω–∫–∏.
   *
   * @param {IconType} key - –ö–ª—é—á, –ø—Ä–µ–¥—Å—Ç–∞–≤–ª—è—é—â–∏–π –∏–∫–æ–Ω–∫—É –¥–ª—è –∏–∑–≤–ª–µ—á–µ–Ω–∏—è.
   * @return {string | undefined} - –°–æ–¥–µ—Ä–∂–∏–º–æ–µ SVG –∏–∫–æ–Ω–∫–∏ –∏–ª–∏ undefined, –µ—Å–ª–∏ –∏–∫–æ–Ω–∫–∞ –Ω–µ –Ω–∞–π–¥–µ–Ω–∞.
   */
  getIcon(key: IconType): string | undefined {
    const icon = key && this.svgMap.get(key);
    if (!icon) {
      return undefined;
    }
    if (!icon.isInitialized) {
      const svg = this.toElement(icon.content);
      icon.content = svg.outerHTML;
      icon.isInitialized = true;
    }
    return icon.content;
  }

  /**
   * –†–µ–≥–∏—Å—Ç—Ä–∏—Ä—É–µ—Ç –æ–¥–Ω—É –∏–ª–∏ –Ω–µ—Å–∫–æ–ª—å–∫–æ –∏–∫–æ–Ω–æ–∫ SvgIcon.
   *
   * @param {SvgIconType|SvgIconType[]} icons - –ò–∫–æ–Ω–∫–∞ SvgIcon –¥–ª—è —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏.
   * @returns {void}
   */
  register(icons: SvgIconType | SvgIconType[]): void {
    for (const { name, data } of Array.isArray(icons) ? icons : [icons]) {
      if (!this.svgMap.has(name)) {
        this.svgMap.set(name, new SvgIcon(data));
      }
    }
  }

  /**
   * –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ—Ç –∑–∞–¥–∞–Ω–Ω—É—é —Å—Ç—Ä–æ–∫—É —Å–æ–¥–µ—Ä–∂–∏–º–æ–≥–æ –≤ SVGElement.
   *
   * @param {string} content - –°—Ç—Ä–æ–∫–∞ —Å–æ–¥–µ—Ä–∂–∏–º–æ–≥–æ –¥–ª—è –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏—è –≤ SVGElement.
   * @returns {SVGElement} - –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–Ω—ã–π SVGElement.
   */
  private toElement(content: string): SVGElement {
    const div = this.document.createElement('div');
    div.innerHTML = this.sanitizer.sanitize(SecurityContext.HTML, this.sanitizer.bypassSecurityTrustHtml(content)) ?? '';
    return div.querySelector('svg') as SVGElement;
  }
}
