import { Attribute, ChangeDetectionStrategy, Component, HostBinding, Input, OnChanges } from '@angular/core';

import { SimpleChangesTyped } from '../../types/simple-changes-typed.type';

/**
 * –ü—Ä–µ–¥—Å—Ç–∞–≤–ª—è–µ—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –∑–∞–≥—Ä—É–∑–∫–∏, –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –ø—Ä–µ–¥–≤–∞—Ä–∏—Ç–µ–ª—å–Ω—ã–π –∑–∞–≥—Ä—É–∑—á–∏–∫.
 */
@Component({
  selector: 'app-loading',
  templateUrl: './loading.component.html',
  styleUrls: ['./loading.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: true,
})
export class LoadingComponent implements OnChanges {
  /**
   * –£–∫–∞–∑—ã–≤–∞–µ—Ç, –≤–∏–¥–∏–º –ª–∏ –≤ –ø—Ä–µ–ª–æ–∞–¥–µ—Ä –∏–ª–∏ –Ω–µ—Ç
   *
   * @type {boolean}
   */
  @Input()
  @HostBinding('class.visible')
  isVisible = false;

  /**
   * –ü—Ä–æ–≤–µ—Ä—è–µ—Ç —è–≤–ª—è—Ç—å—Å—è –ª–∏ —É–∫–∞–∑–∞–Ω–Ω—ã–π –æ–±—ä–µ–∫—Ç –∞–Ω–∏–º–∞—Ü–∏–µ–π.
   *
   * @param {Object} object - –û–±—ä–µ–∫—Ç –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏.
   * @returns {boolean} - –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∏—Å—Ç–∏–Ω—É, –µ—Å–ª–∏ –æ–±—ä–µ–∫—Ç —è–≤–ª—è–µ—Ç—Å—è –∞–Ω–∏–º–∞—Ü–∏–µ–π, –∏–Ω–∞—á–µ –ª–æ–∂—å.
   */
  @HostBinding('class.animation')
  isAnimation = this.type !== 'image';

  /**
   * –ü–æ–ª—É—á–∞–µ—Ç –∑–Ω–∞—á–µ–Ω–∏–µ —Å—Å—ã–ª–∫–∏ –æ—Ç —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–µ–≥–æ –º–µ—Ç–æ–¥–∞.
   * @returns {string} –ó–Ω–∞—á–µ–Ω–∏–µ —Å—Å—ã–ª–∫–∏, –ø–æ–ª—É—á–µ–Ω–Ω–æ–µ –æ—Ç –º–µ—Ç–æ–¥–∞.
   */
  link = this.getLink();

  /**
   * –°–æ–∑–¥–∞–µ—Ç –Ω–æ–≤—ã–π —ç–∫–∑–µ–º–ø–ª—è—Ä –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä–∞.
   *
   * @param {string} type - –¢–∏–ø –æ–±—ä–µ–∫—Ç–∞. –≠—Ç–æ –º–æ–∂–µ—Ç –±—ã—Ç—å 'animation' –∏–ª–∏ 'image'.
   * @return {void}
   */
  constructor(@Attribute('type') public type: 'animation' | 'image' = 'animation') {}

  /**
   * –†–µ–∞–≥–∏—Ä—É–µ—Ç –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤–æ –≤—Ö–æ–¥–Ω—ã—Ö —Å–≤–æ–π—Å—Ç–≤–∞—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞.
   *
   * @param {SimpleChangesTyped<this>} changes - –ò–∑–º–µ–Ω–µ–Ω–∏—è –≤–æ –≤—Ö–æ–¥–Ω—ã—Ö —Å–≤–æ–π—Å—Ç–≤–∞—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞.
   * @return {void} - –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –Ω–∏—á–µ–≥–æ.
   */
  ngOnChanges(changes: SimpleChangesTyped<this>): void {
    if (changes.isVisible?.currentValue && this.type === 'image') {
      this.link = this.getLink();
    }
  }

  /**
   * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Å—Å—ã–ª–∫—É –Ω–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø—Ä–µ–¥–∑–∞–≥—Ä—É–∑—á–∏–∫–∞ —Å –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–º cache buster.
   *
   * @returns {string} –°—Å—ã–ª–∫–∞ –Ω–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø—Ä–µ–¥–∑–∞–≥—Ä—É–∑–∫–∏ —Å –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–º -ant, –ø—Ä–µ–æ–¥–æ–ª–µ–≤–∞—é—â–∏–º –∫–µ—à–∏—Ä–æ–≤–∞–Ω–∏–µ.
   */
  private getLink(): string {
    return `/assets/images/preloader.svg?cacheBuster=${new Date().getTime()}`;
  }
}
