import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { environment } from '../../environments/environment';

/**
 * –ü–µ—Ä–µ—Ö–≤–∞—Ç—ã–≤–∞–µ—Ç HTTP-–∑–∞–ø—Ä–æ—Å—ã –∏ –¥–æ–±–∞–≤–ª—è–µ—Ç –±–∞–∑–æ–≤—ã–π URL-API, –µ—Å–ª–∏ URL-–∑–∞–ø—Ä–æ—Å–∞ –Ω–µ –Ω–∞—á–∏–Ω–∞–µ—Ç—Å—è —Å '/'
 * @class BaseUrlInterceptor
 * @implements {HttpInterceptor}
 * @Injectable({ providedIn: 'root' })
 */
@Injectable({ providedIn: 'root' })
export class BaseUrlInterceptor implements HttpInterceptor {
  /**
   * –ü–µ—Ä–µ—Ö–≤–∞—Ç—ã–≤–∞–µ—Ç HTTP-–∑–∞–ø—Ä–æ—Å—ã –∏ –¥–æ–±–∞–≤–ª—è–µ—Ç –±–∞–∑–æ–≤—ã–π URL API, –µ—Å–ª–∏ URL-–∑–∞–ø—Ä–æ—Å–∞ –Ω–µ –Ω–∞—á–∏–Ω–∞–µ—Ç—Å—è —Å '/'
   * @param {HttpRequest<unknown>} req - HTTP-–∑–∞–ø—Ä–æ—Å –¥–ª—è –ø–µ—Ä–µ—Ö–≤–∞—Ç–∞
   * @param {HttpHandler} next - –°–ª–µ–¥—É—é—â–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ HTTP –≤ —Ü–µ–ø–æ—á–∫–µ –ø–µ—Ä–µ—Ö–≤–∞—Ç–æ–≤
   * @returns {Observable<HttpEvent<unknown>>} - –ù–∞–±–ª—é–¥–∞–µ–º—ã–π –æ–±—ä–µ–∫—Ç, –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–¥–∞—ë—Ç HTTP-–æ—Ç–≤–µ—Ç
   */
  intercept(req: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    req = req.clone({ url: environment.API_URL + req.url });

    return next.handle(req);
  }
}
